@use"../base/_mixin.scss" as *;

.l-feature{
  padding: px-clamp(60,80,375,2000) 0 px-clamp(120,160,375,2000) 0;
  &__body{
    margin-top: px-clamp(30,60,375,2000);
  }
  &__wrapper{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
    column-gap: px-clamp(18,43,375,2000);
  }
}
