@use "../base/_mixin.scss" as *;

.m-section-title{
 display: flex;
 justify-content: space-between;
 align-items: center;
 font-size: px-clamp(20,28,375,2000);
 font-weight: bold;
 white-space: nowrap;
 padding: 0 px-to-rem(10px);
}

.m-section-title::before,
.m-section-title::after{
    border-top: 1px solid;
    content: "";
    width: 20%;
}